import React from 'react';
import { Link } from 'react-router-dom';
import fondo from '../../images/fondo-landing.jpg'
import style from './LandingPage.module.css'

const LandingPage = () =>{
    return(
        <div className={style.box}>
            <img src={fondo} alt="Not found"/>
            <Link to={'/home'}>
                <h1 className={style.title}>POKEMONS APP</h1>
            </Link>
        </div>
    )
}

export default LandingPage;
